export const QBusinessLookupDocument = gql`
  query QBusinessLookup($code: String, $url: String) {
    lookup: companyBranchLookUp(wac: $code, hostUrl: $url) {
      branch: companyBranch {
        ...FCompanyBranch
      }
      isBrand: isBrandWac
      isMobileApp
      knownBasePath
      orderOrigin
      defaultLanguage: userDefaultLanguage
    }
  }

  ${FCompanyBranchFragmentDoc}
`

export function useQBusinessLookup(
  variables?: QBusinessLookupVariables,
  baseOptions?: Hooks.QueryHookOptions<QBusinessLookupVariables>,
) {
  return Hooks.useQuery<QBusinessLookupQuery, QBusinessLookupVariables>(
    QBusinessLookupDocument,
    variables,
    baseOptions,
  )
}

export function useExecutableQBusinessLookup(
  options?: Hooks.ExecutableQueryOptions<QBusinessLookupVariables>,
) {
  return Hooks.useExecutableQuery<
    QBusinessLookupQuery,
    QBusinessLookupVariables
  >(QBusinessLookupDocument, options)
}

