import { Sentry } from '@speedlo/sentry'
import { observer } from 'mobx-react-lite'
import React from 'react'

import { appConfig } from '../config'
import { useQBusinessLookup } from '../graph/generated'
import { useRoot } from './useRoot'

export const BusinessLoader = observer(() => {
  const root = useRoot()
  const { business } = useRoot()

  const { data, loading } = useQBusinessLookup(
    {
      code: business.requestedCode,
      url: window.location.href,
    },
    {
      skip: !root.initialized,
      context: { noBatch: true },
      fetchPolicy: 'cache-first',
    },
  )

  React.useEffect(() => {
    if (loading || business.isValid) {
      return
    }

    if (!data.lookup.branch) {
      Sentry.withScope(scope => {
        scope.setExtras({
          url: window.location.href,
          requestedCode: business.requestedCode,
        })
        Sentry.captureMessage('No business found')
      })
      redirectAway()
      return
    }

    business.updateFromLookup(data.lookup)
    business.log(
      'identified branch %s, brand %s; company %s, isBrand: %s',
      business.branchId,
      business.brandId,
      business.companyId,
      business.isBrand,
    )
  })

  return null
})

function redirectAway() {
  if (process.env.NODE_ENV === 'production') {
    window.location.replace(appConfig.redirectOnFail)
  } else {
    document.write('No business identified')
  }
}
