import { format, formatWithOptions, parse } from 'date-fns/fp'
import csLocale from 'date-fns/locale/cs'

import { FJsonAddressFragment } from '../graph/generated'

export const currentLocale = csLocale

export const formatLocale = formatWithOptions({ locale: currentLocale })

export const formatTime = format('H:mm')

export const dateTimeFormat = 'D.M.YYYY H:mm'
export const formatDateTime = formatLocale(dateTimeFormat)

export const parseTime = parse('HH:mm')

export const formatAddress = (address: FJsonAddressFragment) => {
  if (!address.street) {
    if (address.city) {
      return `${address.city} ${address.houseNumber}`
    }
    return `${address.state}`
  }
  if (!address.houseNumber) {
    return `${address.street}, ${address.city}`
  }
  return `${address.street} ${address.houseNumber}, ${address.city}`
}
