import React from 'react'
import ReactGA from 'react-ga'

import { appConfig } from '../config'

const key = appConfig.analytics
if (key) {
  const debug = process.env.REACT_APP_STAGE !== 'prod'
  ReactGA.initialize(key, {
    debug,
    gaOptions: {
      siteSpeedSampleRate: 50,
    },
  })
  if (debug) {
    ReactGA.ga('set', 'sendHitTask', null)
  }
}

interface IOptions {
  reportPage: boolean
}

export function useAnalytics(opts: Partial<IOptions> = {}) {
  React.useEffect(() => {
    if (opts.reportPage && key) {
      ReactGA.pageview(window.location.href)
    }
  }, [])
  return ReactGA
}
